@import "./assets/styles/reset.css";

@mixin type($ff: "Roboto Mono", $fz: 1.6rem, $fw: 400, $tt: none, $lh: 1.5) {
  font-family: $ff;
  font-size: $fz;
  font-weight: $fw;
  text-transform: $tt;
  line-height: $lh;
}

// variables
:root {
  --white: hsl(0, 0%, 100%);
  --orange: hsl(15, 98%, 55%);
  --gray-dark: hsl(217, 6%, 28%);
  --gray-light: hsl(217, 6%, 70%);
}

// Typography

html {
  font-size: 62.5%;
}

* {
  &::-moz-selection {
    color: var(--white);
    background: var(--gray-dark);
  }
}

* {
  &::-webkit-selection,
  &::selection {
    color: var(--white);
    background: var(--gray-dark);
  }
}

* {
  // firefox
  scrollbar-color: var(--gray-dark) var(--white);
  scrollbar-width: thin;

  // chrome, safari
  &::-webkit-scrollbar {
    width: 0.6rem;
    height: 0.6rem;
  }

  &::-webkit-scrollbar-track {
    background: var(--white);
  }

  ::-webkit-scrollbar-thumb {
    background-color: var(--gray-dark);
    outline: 0px solid var(--gray-dark);
    -webkit-border-radius: 0;
    border-radius: 0;
  }
}

body {
  @include type();
}

form {
  width: 100%;
  position: relative;
}

input:not([type="checkbox"]),
input:not([type="radio"]),
select,
textarea {
  @include type(inherit, 1.6rem, 400, none, 1.3);
  height: 6rem;
  color: var(--gray-dark);
  background: transparent;
  border: 1px solid var(--gray-light);
  border-radius: 0.6rem;
  padding: 0 2rem;
  margin: 0;
  transition: border 0.3s ease;

  &::-moz-placeholder {
    color: var(--gray-dark);
    opacity: 1;
  }
  &::-ms-input-placeholder {
    color: var(--gray-dark);
    opacity: 1;
  }
  &::-webkit-input-placeholder {
    color: var(--gray-dark);
    opacity: 1;
  }

  &:focus {
    border-color: var(--gray-dark);
    &::-moz-placeholder {
      opacity: 0;
    }
    &::-ms-input-placeholder {
      opacity: 0;
    }
    &::-webkit-input-placeholder {
      opacity: 0;
    }
  }
}

textarea {
  height: 14rem;
  padding: 1.5rem 2rem;
}

input[type="file"] {
  padding: 0;
  height: auto;
  width: auto;
  cursor: pointer;
}

#main {
  padding: 5rem 0;
}
