.comment__item {
  position: relative;
  display: flex;
  border-top: 1px solid var(--gray-light);
  border-bottom: 1px solid var(--gray-light);
  margin-top: -1px;
  padding: 2rem 0;

  &_avatar {
    position: relative;
    min-width: 4rem;
    max-width: 4rem;
    margin-right: 1.5rem;

    &_inner {
      padding-top: 100%;
      border-radius: 50%;
      overflow: hidden;
      background-size: 100%;
      background-position: center center;
      background-repeat: no-repeat;
    }
  }

  &_text {
    position: relative;
    font-size: 1.4rem;
  }

  &_title {
    font-weight: 600;
  }
}
