.comments {
  position: relative;

  &__header {
    margin-bottom: 2rem;
  }

  &__body {
    & > div {
      min-height: 20vh;
      display: flex;
      justify-content: center;
      align-items: center;

      & > &:nth-of-type(2) {
        line-height: 0;

        & > div {
          margin-bottom: 2rem;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}
