%lr {
  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;
  }
}

.pagination__box {
  position: relative;
  text-align: center;

  &_list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: inline-flex;
    align-items: flex-start;
    line-height: 0;

    div {
      margin: 0 1rem;
      @extend %lr;

      &.sm {
        a {
          background-color: var(--gray-light);
          border-color: var(--gray-light);
          color: var(--white);
        }

        & ~ div {
          a {
            background-color: var(--gray-light);
            border-color: var(--gray-light);
            color: var(--white);
          }
        }
      }

      &.active {
        a {
          background-color: var(--orange) !important;
          border-color: var(--orange) !important;
          color: var(--white);
          pointer-events: none;
        }

        & ~ div {
          a {
            background-color: var(---gray-dark);
            border-color: var(--gray-dark);
            color: var(--gray-dark);
          }
        }
      }

      &.disabled {
        pointer-events: none;
        opacity: 0.3;
      }
    }
  }

  &_items {
    max-width: calc(4.5rem * 5);
    overflow: hidden;
    position: relative;

    @media only screen and (max-width: 599px) {
      max-width: calc(4rem * 4);
    }

    & > div {
      display: inline-flex;
      align-items: center;
      line-height: 0;
      overflow-x: auto;
      max-width: 100%;
      margin-bottom: -0.4rem;
      padding-bottom: 0.6rem;
    }
  }

  &_control {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 4rem;
    max-width: 4rem;
    min-height: 4rem;
    max-height: 4rem;
    border: 1px solid var(--gray-dark);
    border-radius: 50%;
    transition: all 0.3s ease;
    text-decoration: none;
    color: var(--gray-dark);

    img {
      max-width: 2.4rem;
      position: relative;
      top: -0.1rem;
      left: -0.1rem;
      transition: all 0.3s ease;
    }

    &.next {
      transform: scale(-1, 1);
    }

    &:hover {
      background-color: var(--orange);
      border-color: var(--orange);
      color: var(--white);

      img {
        filter: invert(1);
      }
    }
  }
}
