.form {
  h3 {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1.5rem;
  }

  .field {
    margin-bottom: 2rem;

    &:nth-child(3) {
      margin-bottom: 0;
    }
  }

  &__input {
    position: relative;
  }

  .submit__wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2rem;

    button {
      min-width: 14rem;
      text-transform: uppercase;
    }
  }

  .field {
    span {
      font-size: 1.2rem;
      line-height: 1.3;
      margin: 0;
      color: var(--orange);
      display: none;
    }
  }

  .error {
    border-color: var(--orange);

    & + span {
      display: block;
    }

    &:focus {
      border-color: var(--orange);
    }
  }
}
